@charset "UTF-8";
@keyframes menu-open {
  0% {
    transform: scaleY(0);
    visibility: visible;
  }
  100% {
    transform: scaleY(1);
    visibility: visible;
  }
}
@keyframes menu-close {
  0% {
    transform: scaleY(1);
    visibility: visible;
  }
  99% {
    transform: scaleY(0);
    visibility: visible;
  }
  100% {
    transform: scaleY(0);
    visibility: hidden;
  }
}
@font-face {
  font-family: "Oswald";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/oswaldregular.woff2") format("woff2"), url("../fonts/oswaldregular.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "Oswald";
  font-style: normal;
  font-weight: 500;
  src: url("../fonts/oswaldmedium.woff2") format("woff2"), url("../fonts/oswaldmedium.woff") format("woff");
  font-display: swap;
}
*,
::after,
::before {
  box-sizing: inherit;
}

html {
  box-sizing: border-box;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
  white-space: nowrap;
  clip: rect(0 0 0 0);
  overflow: hidden;
}

img {
  max-width: 100%;
}

.logo {
  background-color: transparent;
  cursor: pointer;
}
.logo:hover {
  opacity: 0.8;
}
.logo:active {
