}

button.button {
  border: none;
  background-color: transparent;
  font-family: inherit;
  padding: 0;
  cursor: pointer;
}

a.button,
button.button {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  background-color: #68b738;
  color: #ffffff;
  border-radius: 0;
  box-shadow: none;
  text-align: center;
  font-weight: 400;
  text-transform: uppercase;
  text-decoration: none;
  transition: 140ms all ease-in-out;
}
@media (max-width: 767px) {
  a.button,
button.button {
    padding: 7px 13px;
    font-size: 14px;
    line-height: 18px;
  }
}
@media (min-width: 768px) {
  a.button,
button.button {
    padding: 14px 26px;
    font-size: 20px;
    line-height: 20px;
  }
}
a.button:hover,
button.button:hover {
  background-color: #5eaa2f;
}
a.button:active,
button.button:active {
  background-color: #5eaa2f;
  color: rgba(255, 255, 255, 0.3);
}
a.button:disabled,
button.button:disabled {
  background-color: #666666;
  cursor: default;
  pointer-events: none;
  user-select: none;
}
a.button--center,
button.button--center {
  width: 100%;
  max-width: 458px;
  align-self: center;
}

a.button:not([href]) {
  background-color: #666666;
  cursor: default;
  pointer-events: none;
  user-select: none;
}

