.main-nav__toggle {
  cursor: pointer;
  font: inherit;
  display: block;
  background-color: transparent;
  border: none;
  margin: 0;
  right: -20px;
  top: 0;
  padding: 24px 20px;
  width: 24px;
  height: 16px;
  position: absolute;
  z-index: 2;
  transition: 140ms all ease-in-out;
  color: #000000;
  box-sizing: content-box;
}
.main-nav__toggle:hover {
  opacity: 0.6;
}
.main-nav__toggle:active {
  opacity: 0.3;
}
.main-nav__toggle--closed::before {
  content: "";
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%);
  width: 24px;
  height: 1.5px;
  background-color: #000000;
  box-shadow: 0 8px 0 0 #000000, 0 -8px 0 0 #000000;
}
.main-nav__toggle--opened::before, .main-nav__toggle--opened::after {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  margin: auto;
  content: "";
  position: absolute;
  width: 25px;
  height: 1.5px;
  background-color: #000000;
  box-shadow: none;
}
.main-nav__toggle--opened::after {
  transform: rotate(45deg);
}
.main-nav__toggle--opened::before {
  transform: rotate(-45deg);
}
@media (min-width: 768px) {
  .main-nav__toggle {
    display: none;
  }
}

@media (max-width: 767px) {
  .main-nav__wrapper {
    width: calc(100% + 40px);
    position: absolute;
    top: 100%;
    right: -20px;
    z-index: -2;
    animation-duration: 0.2s;
    animation-timing-function: ease-in-out;
    animation-fill-mode: forwards;
    transform-origin: 0 0;
    visibility: hidden;
    transform: scaleY(0);
  }
  .main-nav__wrapper--opened {
    animation-name: menu-open;
  }
  .main-nav__wrapper--closed {
    animation-name: menu-close;
  }
}
@media (min-width: 768px) {
  .main-nav__wrapper {
    position: static;
  }
}

.main-nav__list {
