.social__link {
  width: 28px;
  height: 22px;
  position: relative;
  display: block;
  transition: 140ms all ease-in-out;
  background-color: transparent;
  color: #666666;
}
.social__link:hover {
  color: #68b738;
}
.social__link:active {
  color: rgba(104, 183, 56, 0.3);
}

.social__link svg {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  margin: auto;
}

.copyright__link {
  font-family: "Arial", sans-serif;
  color: #444444;
  text-decoration: none;
  transition: 140ms all ease-in-out;
  font-size: 16px;
  line-height: 18px;
}
@media (max-width: 767px) {
  .copyright__link {
    display: flex;
    align-items: center;
  }
}
.copyright__link:active {
  opacity: 0.3;
}
.copyright__link:hover .copyright__link-icon, .copyright__link:active .copyright__link-icon {
  color: #68b738;
}

.copyright__link-icon {
  vertical-align: middle;
  color: #666666;
}
@media (max-width: 767px) {
  .copyright__link-icon {
    margin-left: auto;
  }
}
@media (min-width: 768px) {
  .copyright__link-icon {
    margin-left: 22px;
  }
}

